












import { SNACKBAR } from "@/store/store_types";
import Vue from "vue";
import { mapMutations, mapState } from "vuex";

export default Vue.extend({
  computed: {
    ...mapState("snackBar", {
      message: `message`,
      open: `openSnackbar`,
    }),
  },
  methods: {
    ...mapMutations(SNACKBAR, ["hideSnackbar"]),
  },
  data: () => ({
    timeout: 1500,
  }),
  watch: {
    open() {
      setTimeout(() => {
        this.hideSnackbar();
      }, this.timeout);
    },
  },
});
